h1 {
  margin-top: 0;
  margin-bottom: 0;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
}

h1 > * {
  margin: 5px;
}

h2 {
  text-align: center;
  margin-top: 0;
  margin-bottom: 0;
}

h3 {
  margin-top: 0;
  margin-bottom: 0;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  height: 30px;
}

h3 > * {
  margin: 5px;
}

input[type='button'] {
  padding: 5px;
  border: 3px solid #636363;
  border-radius: 5px;
}

input[type='button']:hover {
  background-color: #979797;
}

.highlightText {
  color: mediumblue;
  font-weight: 700;
}
